.btn{
    font-size: rem(14px);
    line-height:rem(16px);
    letter-spacing: 0.04em;
    padding: rem(15px 23px);
    border-radius: 0;
    text-transform: uppercase;
    &:focus{
        box-shadow:none;
        outline:0 none;
    }
    &-sm{
        padding: rem(8px 14px);
    }
    &-primary{
        &-filled{
            color: $white;
            background-color: $navy;
            &:hover{
                color:$true-black;
                background:$aquamarine-100;
            }
        }
        &-ghost{
            color:var(--bs-baby-blue);
            border-color:var(--bs-baby-blue);
            background:transparent;
            &:hover{
                color:$true-black;
                background:$aquamarine-100;
            }
        }

    }
    &-neutral{
        color:var(--bs-true-black);
        border-color: var(--bs-true-black);
        background: transparent;
        &:hover{
            color:$true-black;
            background:$aquamarine-100;
        }
    }
}