.container-fluid {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;


  @include media-breakpoint-up(lg) {
    padding-left: rem(24px);
    padding-right: rem(24px);
  }
  @include media-breakpoint-up(xl) {
    padding-left: rem(24px);
    padding-right: rem(24px);
  }


  &--fullwidthheader {
    max-width: 1200px;
  }

  &--fullwidthstat {
    @include media-breakpoint-up(lg) {
      max-width: 808px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 1200px;
    }

    .row {
      @include media-breakpoint-up(lg) {
        display: grid;
      }
      @include media-breakpoint-up(xl) {
        display: flex;
      }
    }
  }

  &--fullwidth {
    @include media-breakpoint-up(lg) {
      max-width: 808px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 1004px;
    }
  }
  &--fullstatwidth {
    @include media-breakpoint-up(lg) {
      max-width: 808px;
    }
    @include media-breakpoint-up(xl) {
      max-width: 1200px;
    }
  }
}

.content-list {
  position: relative;
  background-color: $navy-light;

  > div {
    position: relative;
    max-width: 662px;
  }

  &::before {
    content: '';
    position: absolute;
    width: 882px;
    height: 100%;
    left: calc(-9.333%);
    top: 0;
    background-color: $navy-light;
    @include media-breakpoint-up(lg) {
      width: 882px;
      left: calc(-9.333% - 0px);

    }
    @include media-breakpoint-up(xl) {
      width: 882px;
      left: calc(-9.333% - 24px);

    }
  }
}

.grid-74-9 {
  display: grid;
  grid-template-columns: repeat(var(--bs-columns, 9), 74px);
  grid-gap: 1.5rem;

  > .container-content {
    grid-column: auto/span 8;
    grid-column-start: 2;
  }
}
