@import '../common-imports';

.leadspace {
    z-index: 1;
    padding-bottom: rem(80px);
    @include media-breakpoint-up(tb) {
        padding-bottom: 0;
    }


    &.text-small{
        .container-content h1{
            max-inline-size: 17ch;
        }
    }
    .container {
        &--fullwidthheader {
            @include media-breakpoint-up(tb) {
                padding-left: rem(100px);
                padding-right: rem(100px);
            }
            @include media-breakpoint-up(xl) {
                padding-left: rem(24px);
                padding-right: rem(24px);
            }
        }
    }
    .container-content {
        @include media-breakpoint-up(tb) {
            padding-top: rem(36px);
            padding-bottom: rem(36px);
            padding-left: rem(30px);
            min-height: rem(496px);
            justify-content: center;
            display: flex;
            flex-direction: column;
        }
        @include media-breakpoint-up(xl) {
            padding-left: 0;
        }

        h1 {
            max-inline-size: 19ch;
        }
        .body-2 {
            max-inline-size: 50ch;
        }
    }

    &-home {
        z-index: 2;
        overflow-y: visible;
    }

    &-image {
        &--ratio {
            margin: 0 -1.5rem;
            position: relative;
            width: calc(100% + 48px);
            margin-bottom: rem(64px);
            @include media-breakpoint-up(tb) {
                margin-bottom: 0;
                height: 100%;
                width: calc(100% + 142px);
                margin-left: rem(-124px);
            }

            @include media-breakpoint-up(xl) {
                width: calc(100% + 24px);
                margin: 0;
                margin-left: rem(-24px);
            }

            img {
                position: absolute;
                width: 100%;
                height: 100%;
                left: 0;
                object-fit: cover;
            }
        }
    }
}

@keyframes rotation {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.logo {
    &-rotate {
        display: none;
        position: absolute;
        z-index: 5;
        transform: translate(50%, 50%);
        @include media-breakpoint-up(md){
            display: block;
            width: rem(188px);
        }
        @include media-breakpoint-up(xl){
            width: rem(256px);
        }



        &--rightTop{
            right: 0;
            top: 0;
            transform: translate(50%, -50%);
        }
        &--rightBottom{
            right: 0;
            bottom: 0;
            transform: translate(50%, 50%);
        }
        &--leftBottom{
            left: 0;
            bottom: 0;
            transform: translate(-50%, 50%);
        }
        &--leftTop{
            left: 0;
            top: 0;
            transform: translate(-50%, -50%);
        }
        &--leftCenter{
            left: 0;
            bottom: 50%;
            transform: translate(-50%, 50%);
        }
        &--rightCenter{
            right: 0;
            bottom: 50%;
            transform: translate(50%, 50%);
        }
        svg {
            animation: rotation 10s infinite;
            transition-timing-function: ease;
            width: 100%;
            height: 100%;
        }
    }
}
