.stat {
  &-two {
    &-column {
      padding: rem(80px 0);
      //@include media-breakpoint-up(md) {
      //  padding: rem(124px 0);
      //}

      &--dark {
        background: $coal;
        color: $baby-blue;
      }

      &--light {
        background: $diamond;
        color: $true-black;
      }

      &--left {
        grid-column-start: 1;
        grid-column-end: span 12;
        @include media-breakpoint-up(md) {
          grid-column-start: 1;
          grid-column-end: span 3;
        }
        @include media-breakpoint-up(xl) {
          grid-column-start: 3;
          grid-column-end: span 3;
        }

      }

      &--right {
        grid-column-start: 1;
        grid-column-end: span 12;
        @include media-breakpoint-up(md) {
          grid-column-start: 4;
          grid-column-end: span 5;
        }
        @include media-breakpoint-up(xl) {
          grid-column-start: 7;
          grid-column-end: span 5;
        }
      }

      &--fullwidth {
        grid-column-start: 1;
        grid-column-end: span 12;
        @include media-breakpoint-up(md) {
          grid-column-start: 1;
          grid-column-end: span 8;
        }
        @include media-breakpoint-up(xl) {
          grid-column-start: 3;
          grid-column-end: span 8;
        }
      }

      &-wrapper {
        display: grid;
        grid-template-columns: repeat(12, 1fr);
        margin: 0;
        grid-row-gap: 80px;
        grid-column-gap: 24px;
        grid-row-gap: 0;
        @include media-breakpoint-up(md) {
          grid-template-columns: repeat(8, 1fr);
        }
        @include media-breakpoint-up(xl) {
          grid-template-columns: repeat(12, 1fr);
        }
        > * + * {
          margin-top: rem(80px);
          @include media-breakpoint-up(md) {
            margin-top: rem(0px);
          }

        }


      }

      &--fullwidthstat {
        max-width: 808px;
        @include media-breakpoint-up(xl) {
          max-width: 1200px;
        }

        > * + * {
          margin-top: rem(80px);
          @include media-breakpoint-up(xl) {
            margin-top: rem(120px);
          }

        }
      }

      .stat-one-column {
        > * + * {
          margin-top: rem(40px);
        }
      }

      &__main {
        margin-bottom: rem(48px);
        padding-bottom: rem(48px);
        border-bottom: 1px solid rgba($graphite, .5);

        &:last-child {
          border-bottom: none;
          padding-bottom: 0;
          margin-bottom: 0;
        }

        .loop {
          display: grid;
          grid-template-columns: repeat(1,1fr);
          grid-gap: 24px;
          justify-content: flex-start;
          @include media-breakpoint-up(tb) {
            grid-template-columns: repeat(auto-fill, minmax(120px, 120px));
          }
        }
      }


    }
  }
}


.c {
  &-stat {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    > * + * {
      margin-top: rem(16px);
      margin-bottom: 0;
    }

    &--small {
      justify-content: flex-start;

      > * + * {
        margin-top: rem(8px);
        margin-bottom: 0;
      }
    }


    .display {
      display: inline-flex;
      align-items: baseline;
      gap: 10px;
      margin-bottom: 0;


      span {
        height: fit-content;
        display: flex;
      }

      &.reverse {
        flex-direction: row-reverse;
        justify-content: flex-end;
      }

      &--hasLottie {
        gap: 16px;
        align-items: center;
      }
    }

    .menu-1 {
      margin-bottom: 0;
      font-weight       : 400;
            font-size         : rem(80px);
            line-height       : 1.48;
            letter-spacing    : 0.03em;
            font-family       : $font-family-heading;
            @include media-breakpoint-up(tb) {
                font-weight       : 400;
                font-size         : rem(40px);
                line-height       : 1.6;
                text-transform    : none;
                letter-spacing    : normal;
            }
    }

    .subtitle-4{
      font-size         : rem(24px);
            line-height       : 1.33;
            letter-spacing    : 0.02em;
            text-transform    : none;
            @include media-breakpoint-up(tb) {
                font-size         : rem(18px);
                line-height       : 1.33;
                letter-spacing    : 0.02em;
                text-transform    : none;
            }
    }

    .lottie-display {
      width: 96px;
      display: flex;
    }
  }
}




.quote{
  > * + * {
    margin-top: rem(80px);
    @include media-breakpoint-up(md) {
      margin-top: rem(120px);
    }
  }


  .hasParagraph{
    p{
      margin-top: rem(40px);
      font-size: rem(18px);
      line-height: 1.78;
      letter-spacing: 0.02em;
      text-transform: none;
    }
  }

  .logo-rotate{
    margin: 0;
  }

  .eyebrow{
    margin-bottom: 1.5rem;
  }

  &.is-dark{
    ul,
    ol {
      padding-left: 2rem;

      > li {
        padding-left: .4rem;
        margin-bottom: rem(40px);

        &:last-child {
          margin-bottom: 0;
        }

        &::marker {
          color: $white;
          font-size: rem(20px);
        }
      }
    }
  }
  &.is-light{
    ul,
    ol {
      padding-left: 2rem;

      > li {
        padding-left: .4rem;
        margin-bottom: rem(40px);

        &:last-child {
          margin-bottom: 0;
        }

        &::marker {
          font-size: rem(20px);
          color: $true-black;
        }
      }
    }
  }

}
