h1,
h2,
h3,
.display,
.subtitle-1,
.menu-1,
.menu-2 {
    @extend %heading;
}

.display {
    font-weight: 400;
    font-size: rem(80px);
    line-height: 1.48;
    letter-spacing: 0.03em;
}

h1,
.h1 {
    font-size: rem(64px);
    font-weight: 500;
    line-height: 1.25;
    text-transform: uppercase;
}

h2,
.h2 {
    font-size: rem(56px);
    font-weight: 500;
    line-height: 1.48;
    letter-spacing: 0.02em;
    text-transform: uppercase;
}

h3,
.h3 {
    font-weight: 500;
    font-size: rem(48px);
    line-height: 1.33;
    text-transform: none;
    letter-spacing: normal;
}

.subtitle-1 {
    font-weight: 400;
    font-size: rem(48px);
    line-height: 1.33;
    text-transform: none;
    letter-spacing: normal;
}

.menu-1 {
    font-weight: 400;
    font-size: rem(40px);
    line-height: 1.6;
    text-transform: none;
    letter-spacing: normal;
}

.menu-2 {
    font-size: rem(32px);
    line-height: 1.5;
    text-transform: none;
    letter-spacing: normal;
}

.eyebrow {
    font-size: rem(24px);
    line-height: 1.08;
    letter-spacing: 0.04em;
    text-transform: uppercase;
}

.subtitle-2 {
    font-size: rem(32px);
    line-height: 1;
    letter-spacing: 0.02em;
    text-transform: none;
}

.subtitle-3 {
    font-size: rem(24px);
    line-height: 1.33;
    letter-spacing: 0.02em;
    text-transform: none;
}

.subtitle-4 {
    font-size: rem(18px);
    line-height: 1.33;
    letter-spacing: 0.02em;
    text-transform: none;
}

.body {
    font-size: rem(20px);
    line-height: 1.6;
    letter-spacing: 0.02em;
    text-transform: none;

    &-2 {
        font-size: rem(18px);
        line-height: 1.78;
        letter-spacing: 0.02em;
        text-transform: none;
    }
    &-3 {
        font-size: rem(14px);
        line-height: 1.14;
        letter-spacing: 0.02em;
        text-transform: none;
    }
}

.button {
    font-size: rem(14px);
    line-height: rem(16px);
    letter-spacing: 0.04em;
    text-transform: uppercase;
    &-2 {
        font-weight: 700;
        font-size: rem(16px);
        line-height: 1.5;
        letter-spacing: 0.02em;
        text-transform: none;
    }
}
