.#{$global} {
  &-footer {
    &__top {
      row-gap: rem(24px);
      @include media-breakpoint-up(tb) {
        row-gap: 0;
        padding-bottom: rem(40px);
        margin-bottom:  rem(40px);
        border-bottom: 1px solid $graphite;
      }

      &--left {
        figure {
          a {
            display: block;

            img {
              height: rem(30px);
              width: auto;
              display: block;
            }

          }
        }

        .body-3 {
          text-decoration: none;
          position: relative;
          padding-left: rem(16px);
          margin-right: rem(40px);
          color: $baby-blue;
          white-space: nowrap;
          text-decoration: none;
          @extend .transition-fast;

          &::before {
            position: absolute;
            content: '';
            height: rem(16px);
            width: 1px;
            background-color: $baby-blue;
            top: 0;
            bottom: 0;
            margin: auto 0;
            left: 0;
          }

          &:hover {
            color: $aquamarine-100;
          }
        }
      }

      &--right {

      }
    }

    .border {
      border-color: $graphite !important;
    }

    .social-icons {
      margin-right: rem(0px);
      list-style: none;
      display: inline-flex;
      align-items: center;
      gap: rem(16px);
      @include media-breakpoint-up(tb) {
        gap: rem(24px);
      }

      li {
        margin: 0;
        padding: 0;

        &:last-child {
          margin: 0;
        }

        a {
          svg{
            @extend .transition-fast;
          }
          path {
            @extend .transition-fast;
          }

          &:hover {
            svg {
              fill: $aquamarine-100;
              path{
              fill: $aquamarine-100;
              }
            }
          }
        }
      }
    }

    &__bottom {
      column-gap: rem(53px);

      .company-name {
        color: $baby-blue;
        @include media-breakpoint-up(md) {
          margin: 0;
        }

        &:hover {
          color: $aquamarine-100;
          text-decoration: underline;
        }
      }

      ul {
        font-size: 0;
        li {
          padding-left: 0px;
          padding-right: rem(20px);

          &:last-child {
            padding-right: 0px;
          }

          a {
            text-decoration: underline;
            color: $navy-light;
            text-transform: capitalize;
            @extend .transition-fast;

            &:hover {
              color: $aquamarine-100;

              svg {
                path {
                  fill: $aquamarine-100;
                }
              }
            }
          }

          &:last-child {
            a {
              margin-right: 0;
            }
          }
        }
      }
    }
  }
}


