html {
  // scroll-behavior: smooth;
  font-size: 16px;
  height: 100%;
}

html,
body {
  height: auto;
  position: relative;
  -webkit-overflow-scrolling: touch;
  // Makes the smooth scrolling on ios devices
}


body {
  letter-spacing: normal;
  @include font-smoothing(on);
  font-size: rem(18px);
  line-height: 1.78;
  letter-spacing: 0.02em;
  font-family: $font-family-base;

  &.prevent-scrolling {
    @extend .overflow-hidden, .position-fixed, .w-100;
    top: auto;
  }

  // &.loading{
  //     opacity: 0;
  // }
}

a {
  text-decoration: underline;
  color: var(--bs-baby-blue);


  &:hover,
  &:focus {
    text-decoration: none;
    color: var(--bs-aquamarine-100);
    text-decoration: none;
  }
}


p {
  margin-bottom: rem(40px);

  &:last-child {
    @extend .mb-0;
  }
}

ul,
ol {
  padding-left: 2rem;

  > li {
    padding-left: .4rem;
    margin-bottom: 1rem;

    &:last-child {
      margin-bottom: 0;
    }

    &::marker {
      color: $navy;
    }
  }
}

b,
strong {
  font-weight: $font-weight-bold;
}

figure {
  margin-bottom: 0;
}

button,
.btn,
.btn-link {
  text-decoration: none;

  &:hover {
    text-decoration: none;
    outline: none;
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  box-shadow: 0 0 0 13rem $white inset !important;
}


/* Page Loader */
.loading::before,
.loading::after {
  content: '';
  position: fixed;
  z-index: 5000;
}

.loading::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $true-black;
}

.loading::after {
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin: -30px 0 0 -30px;
  border-radius: 50%;
  opacity: 0.4;
  background: $aquamarine-100;
  animation: loaderAnim 0.7s linear infinite alternate forwards;

}

@keyframes loaderAnim {
  to {
    opacity: 1;
    transform: scale3d(0.5, 0.5, 1);
  }
}


// bg utilities
.bg-navy-light-gradient {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4)), #EDF7FE;
}

.bg-light-blue-withOpacity {
  background: rgba(237, 247, 254, 0.5);
}

.bg-navy-dark-gradient {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.4)), #EDF7FE;
}

.font-variant-numeric--oldstyle-nums {
  font-variant-numeric: oldstyle-nums;
}

.font-variant--all-small-caps {
  font-variant: all-small-caps;
}

