.m-main-navigation {
  z-index: 999;
}

body.is-active-menu {
  position: fixed;
  width: 100%;
  height: 100%;
}

body.is-active-menu-delayed {
  .theme-header {
    background-color: var(--bs-true-black);
  }
}

.m {
  &-main {
    &-navigation {
      pointer-events: none;
      visibility: hidden;
      transform: translateZ(0);
      will-change: visibility;
      transition: visibility 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
      color: var(--bs-baby-blue);
      height: 100vh;

      &--scroll {
        .is-menu-active & {
          pointer-events: all;
          visibility: visible;
          overflow: auto;
          padding-top: 84px;
          padding-bottom: 100px;
          height: 100vh;
          overflow-x: hidden;
          @include media-breakpoint-up(lg) {
            padding-bottom: 0;
          }
        }

        .is-active-menu-body & {
          &:before {
            transform: skew(0deg) scaleX(1) translateZ(0);
          }
        }

        &:before {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: var(--bs-true-black);
          transform-origin: bottom right;
          transform: skew(-10deg) scaleX(0) translateZ(0);
          transition: transform 0.8s cubic-bezier(0.77, 0, 0.175, 1);

        }
      }


      &.is-menu-active {
        a {
          transform: translateZ(0);
          opacity: 1;
          transition: transform 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) 0.7s,
          opacity 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) 0.7s;
          position: relative;

          &.current {
            color: $aquamarine-100;
            pointer-events: all;

            &:before {
              display: none;
            }
          }
        }
      }

      a {
        //@extend %heading;
        font-family: $headings-font-family;
        display: flex;
        align-items: center;
        border-bottom: 1px solid $diamond;
        text-decoration: none;
        padding-top: rem(32px);
        padding-bottom: rem(32px);
        color: $baby-blue;
        margin: 0;
        opacity: 0;
        transform: translateY(80px) translateZ(0);
        will-change: transform, opacity;
        transition: opacity 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22), transform 0s linear 0.2s,
        background-color 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22) 0.2s;
        font-size: rem(32px);
        line-height: 1.5;
        @include media-breakpoint-up(tb) {
          font-size: rem(40px);
          line-height: 1.6;
        }

        &:hover,
        &.active {
          color: $aquamarine-100;
        }

        &:first-child {
          padding-top: rem(40px);
        }


        &:last-child {
          border-bottom: none;
        }

        > span.wrap {
          display: flex;
          flex-direction: column;
          text-align: center;
          width: 100%;

        }
      }
    }
  }
}