.#{$global} {
    &-header { 
        z-index: 1000;
        position: sticky;
        top: 0;
        .navbar {
            padding: 0;
            height: 84px;
            opacity: 0;
            justify-content: unset;
            @include media-breakpoint-up(tb) {
                justify-content: space-between;
            }
            gap: rem(15px);
            &-brand {
                margin: 0;
                @include media-breakpoint-down(tb) { 
                    width: rem(32px);
                    overflow: hidden;
                }
            }
            .primary-links {
                text-align: center;
                z-index: -4;
                font-weight: 400;
                @include media-breakpoint-up(tb) { 
                    position: absolute;
                    width: 100%;
                }
                a{
                    text-decoration: none;
                    color: $baby-blue;
                    &:hover{
                        color: $aquamarine-100;
                        
                    }
                }
            }
            &-border {
                border-bottom: 1px solid $graphite;
                transition: border 0.8s cubic-bezier(0.165, 0.84, 0.44, 1);
            }
        }
    }
}
